
#wrapper {
  padding-left: 13%;
  padding-right: 13%;
}
.market {
  float: left;
}
#firstMarket {
  height: 337px;
  width: 560px;
  margin: auto;
}
#secondMarket {
  height: 327px;
  width: 540px;
  margin: auto;
}
#thirdMarket {
  height: 357px;
  width: 540px;
  margin: auto;
}
#fourthMarket {
  height: 327px;
  width: 520px;
  margin: auto;
}
.markets {
  width: 1000px;
  text-align: center;
}
@media screen and (max-width:1200px){
.markets {
    width: 800px;
}
}
@media screen and (max-width:875px){
.markets {
    width: 600px;
}
}
@media screen and (max-width:700px){
.markets {
    position: relative;
    right: 15%;
}
}



