
#pinfo {
  padding-left: 33%;
  display: inline-block;
}
#personalPhoto {
  width: 200px;
  height: 200px;
  float: right;
  position: relative;
  bottom: 170px;
}
#personalP {
  position: relative;
  right: 33%;
}
@media screen and (max-width:1300px){
#personalPhoto {
    position: relative;
    bottom: 210px;
}
}
@media screen and (max-width:1050px){
#personalPhoto {
    position: relative;
    bottom: 245px;
    width: 185px;
    height: 185px;
}
}
@media screen and (max-width:1050px){
#personalPhoto {
    position: relative;
    bottom: 245px;
    width: 185px;
    height: 185px;
}
}
@media screen and (max-width:870px){
#personalP {
    position: relative;
    right: 50%;
}
}
@media screen and (max-width:581px){
#personalPhoto {
    visibility: hidden;
}
}



