
.slots{
  width: 100px;
  height: 140px;
  display: inline-block;
}
#matchingGame{
 width: 700px;
 height: 800px;
 position: relative;
 left: 13%;
}
#time{
  font-size: 35px;
  position: relative;
  left: 20%;
}
#score{
  position: relative;
  right: 20%;
  font-size: 35px;
  bottom: 46px;
}
@media screen and (max-width:1000px){
#matchingGame{
    position: relative;
    right: 6%;
    width: 400px;
}
}



