
#infoTab{
  width: 1300px;
  margin-left: auto;
  margin-right: auto;
  /*  border: 4px solid; */
  text-align: center;
}
.nav{
  position: relative;
}
@media screen and (max-width:1300px){
#infoTab{
    width: 1100px;
}
}
@media screen and (max-width:1100px){
#infoTab{
    width: 1030px;
}
}
@media screen and (max-width:1050px){
#infoTab{
    width: 900px;
}
}
@media screen and (max-width:870px){
#infoTab{
    width: 700px;
}
}
@media screen and (max-width:700px){
#infoTab{
    width: 600px;
}
}
@media screen and (max-width:580px){
#infoTab{
    width: 500px;
}
}
body{
  background-color: #e1e1d0;
}




